@mixin background($size: contain, $position: 50% 50%, $repeat: no-repeat, $attachment: scroll, $color: transparent) {
	background: $repeat $attachment $position $color;
	background-size: $size;
}

@mixin clamp($count: 1) {
	overflow: hidden;
	text-overflow: ellipsis;
	
	@if ($count == 1) {
		white-space: nowrap;
	}
	@else {
		max-height: 1.5em * $count;
		display: -webkit-box;
		-webkit-line-clamp: $count;
		-webkit-box-orient: vertical;
	}
}

@keyframes wiggle {
	0% {transform:rotate(-30deg);}
	50% {transform:rotate(30deg);}
	100% {transform:rotate(-30deg);}
}

@keyframes fadeOut {
  0%   { opacity: 0; }
  50% { opacity: 1; }
	100% { opacity: 0; }
}

@keyframes scale {
    0% {
        transform: scale(1.0);
    }
    50% {
        transform: scale(2.0);
    }
		100% {
        transform: scale(1.0);
    }
}

.main {
}

.message {
	padding-top: 1em;
	font-size: 2em;
	text-align: center;
}

.question {
	font-size: 5em;
	font-weight: bold;
	text-align: center;
	animation: scale 10s infinite;
}

.q1 {
	animation: fadeOut 4s infinite;
	animation-delay: 2s;
}

.q11 {
	animation: wiggle 10s infinite;
}

.top {
	margin-bottom: .5em;
}

.frodo {
	position: relative;
	overflow: hidden;
	
	&:after {
		content: "";
		display: block;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		@include background();
		background-image: url("./assets/images/frodo.png");
	}
}

.arms {
	padding-top: 140px;
	padding-bottom: 140px;
	width: 40%;
	display: block;
	margin: 0 auto;
	animation: wiggle 5s infinite;
}



