@mixin background($size: contain, $position: 50% 50%, $repeat: no-repeat, $attachment: scroll, $color: transparent) {
	background: $repeat $attachment $position $color;
	background-size: $size;
}

@mixin clamp($count: 1) {
	overflow: hidden;
	text-overflow: ellipsis;
	
	@if ($count == 1) {
		white-space: nowrap;
	}
	@else {
		max-height: 1.5em * $count;
		display: -webkit-box;
		-webkit-line-clamp: $count;
		-webkit-box-orient: vertical;
	}
}

.main {
	
}

.progress {
	transition: width 1s;
	white-space: nowrap;
	
	&.hide {
		width: 0 !important;
	}
}

.container {
	padding: 2rem;
	
	.column {
		display: inline-block;
		width: 50%;
		vertical-align: top;
		
		.header {
			font-family: Dosis, arial, sans-serif;
			font-size: 1.25rem;
			color: black;
		}
	}
}

.skills {
	display: block;
	margin: 0 auto;
	width: 75%;
}

.content {
	padding: 10rem;
}

.row {
	width: 100%;
}



