@mixin background($size: contain, $position: 50% 50%, $repeat: no-repeat, $attachment: scroll, $color: transparent) {
	background: $repeat $attachment $position $color;
	background-size: $size;
}

@mixin clamp($count: 1) {
	overflow: hidden;
	text-overflow: ellipsis;
	
	@if ($count == 1) {
		white-space: nowrap;
	}
	@else {
		max-height: 1.5em * $count;
		display: -webkit-box;
		-webkit-line-clamp: $count;
		-webkit-box-orient: vertical;
	}
}

.main {
	background-color: #111;

	.bio {
		padding-bottom: 0 !important;
	}

	.titles {
		font-size: 2rem !important;
	}

	.profilePic {
		filter: grayscale(100%);
		transition: 0.5s ease-in-out;

		&:hover {
			filter:grayscale(0%);
			transition: 0.5s ease-in-out;
		}
	}
}



