@mixin background($size: contain, $position: 50% 50%, $repeat: no-repeat, $attachment: scroll, $color: transparent) {
	background: $repeat $attachment $position $color;
	background-size: $size;
}

@mixin clamp($count: 1) {
	overflow: hidden;
	text-overflow: ellipsis;
	
	@if ($count == 1) {
		white-space: nowrap;
	}
	@else {
		max-height: 1.5em * $count;
		display: -webkit-box;
		-webkit-line-clamp: $count;
		-webkit-box-orient: vertical;
	}
}

.logo {
	width: 100%;
	height: 119px;
	
	@include background();
	background-image: url('./assets/images/robby-logo-white.png');
	text-indent: -9999px;
}

.title {
	color: white;
}

.name {
	font-size: 1.25em;
}

.tagline {
	margin-top: 2rem;
	font-size: .75em;
}

.navigation {
	:global(.fa.fa-angle-down) {
		transition: transform 0.25s;
		&:before {
			margin-left: 2px;
		}
	}
}



